.dash-right-pane {
  background-color: #0e181e;
  margin: 0px !important;
  padding:0px !important;
}

.main-viz {
  background-color: #0E1A1F;
  height: 50vh;
  padding-top: 3%;
  padding-left: 3%;
}

.card-viz {
  position: relative;
  bottom: 110px;
}

.card-viz .card {
  box-shadow: 2px 2px 2px grey;
  padding: 0px !important;
  font-family: 'Aleo', serif;

}

.card-viz .card-title {
  text-align: center;
  padding-top: 10%;
  padding-bottom: 10%;
  font-weight: 400;
  font-size: 120%;
  color: #0F1A20;
}

.card-stat {
  text-align: center;
  padding-top: 8%;
  padding-bottom: 10%;
  color: #F9423A;
  font-weight: 700;
  font-size: 180%;
}

.large-icon {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.js-plotly-plot {
  display: block;
  margin-left: auto;
  margin-right: auto;
}